
.search-form {
  background-color: #333333;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 40px;
}

.search-form input {
  padding: 10px;
  border-radius: 10px;
  outline: none;
  border-color: #333333;
  margin-right: 0;
}

.wrapper {
  width: 350px;
  margin: 0 auto;
  padding: 20px 0;
}

.gallery {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
}

.gallery-item {
  margin-right: 0;
  margin-left: 10px;
  margin-bottom: 15px;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}

.gallery-image {
  width: 330px;
  height: 370px;
  object-fit: cover;
  margin-bottom: 15px;
}

@media screen and (min-width: 768px) {
  .wrapper {
    width: 738px;
  }
  .gallery-image {
    width: 360px;
  }
  .gallery-item {
    margin-right: 15px;
  }
  .gallery-item:nth-child(2n) {
    margin-right: 0;
  }
  .search-form button,
  .wrapper > button {
    margin-top: 0;
  }
  .search-form input {
    margin-right: 10px;
  }
}

@media screen and (min-width: 1024px) {
  .wrapper {
    width: 994px;
  }
  .gallery-image {
    width: 480px;
  }
}

@media screen and (min-width: 1280px) {
  .wrapper {
    width: 1250px;
  }
  .gallery-item:nth-child(2n) {
    margin-right: 15px;
  }
  .gallery-item:nth-child(4n) {
    margin-right: 0;
  }
}

.load-more {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  margin-top: 40px;
  margin-bottom: 40px;
  min-width: 73px;
  border-radius: 4px;
  padding: 10px 22px;
  
  
  font-weight: 500;
  font-size: 16px;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.03em;
  
  border: none;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    color: white;
    background-color: forestgreen ; }
}

